import React, { useState, useEffect } from 'react';
import { Route, Switch, Redirect, useHistory } from 'react-router-dom';
import Header from './Header';
import Main from './Main';
import Footer from './Footer';
import ImagePopup from './ImagePopup';
import api from '../utils/api';
import { CurrentUserContext } from '../contexts/CurrentUserContext'
import EditProfilePopup from './EditProfilePopup';
import EditAvatarPopup from './EditAvatarPopup';
import AddPlacePopup from './AddPlacePopup';
import DeleteCardPopup from './DeleteCardPopup';
import Login from './Login';
import ProtectedRoute from './ProtectedRoute';
import Register from './Register';
import InfoTooltip from './InfoTooltip';
import * as auth from '../utils/auth';
import '../index.css';

function App() {
  const [cards, setCards] = useState([])

  const [currentUser, setCurrentUser] = useState({})

  function handleRequestAllData() {
    api.getAllNeededData() // Ð²Ð¾Ð·Ð²ÑÐ°ÑÐ°ÐµÑ ÑÐµÐ·ÑÐ»ÑÑÐ°Ñ Ð¸ÑÐ¿Ð¾Ð»Ð½ÐµÐ½Ð¸Ñ Ð½ÑÐ¶Ð½ÑÑ Ð¿ÑÐ¾Ð¼Ð¸ÑÐ¾Ð² (ÐºÐ°ÑÑÐ¾ÑÐºÐ¸ Ð¸ Ð¸Ð½ÑÐ¾ÑÐ¼Ð°ÑÐ¸Ð¸ Ð¿Ð¾Ð»ÑÐ·Ð¾Ð²Ð°ÑÐµÐ»Ñ)
      .then(([cards, userData]) => {
        setCurrentUser(userData)
        setCards(cards)
      })
      .catch((err) => console.log(err))
  }

  useEffect(() => {
    handleRequestAllData()
  }, [])

  function handleCardLike(card) {
    // ÐÑÐ¾Ð²ÐµÑÑÐµÐ¼, ÐµÑÑÑ Ð»Ð¸ ÑÐ¶Ðµ Ð»Ð°Ð¹Ðº Ð½Ð° ÑÑÐ¾Ð¹ ÐºÐ°ÑÑÐ¾ÑÐºÐµ
    const isLiked = card.likes.some(i => i._id === currentUser._id);

    if (!isLiked) {
      api.like(card._id)
        .then((newCard) => {
          setCards((state) => state.map((c) => c._id === card._id ? newCard : c));
        })
        .catch(err => console.log(err));
    } else {
      api.dislike(card._id)
        .then((newCard) => {
          setCards((state) => state.map((c) => c._id === card._id ? newCard : c));
        })
        .catch(err => console.log(err));
    }
  } 

  function handleUpdateUser(dataUser) {
    api.setUserInfo(dataUser)
      .then((res) => {
        setCurrentUser(res)
        closeAllPopups()
      })
      .catch(err => console.log(err))
  }

  function handleUpdateAvatar(avaLink) {
    api.setUserAvatar(avaLink)
      .then((res) => {
        setCurrentUser(res)
        closeAllPopups()
      })
      .catch(err => console.log(err))
  }

  function handleAddPlaceSubmit(dataCard) {
    return api.addCard(dataCard)
      .then((newCard) => {
        setCards([newCard, ...cards])
        closeAllPopups()
      })
      .catch(err => console.log(err))
  }

  // Ð¡ÑÐµÐ¹ÑÑ Ð¾ÑÐºÑÑÑÐ¸Ñ Ð¿Ð¾Ð¿Ð°Ð¿Ð¾Ð²
  const [isEditAvatarPopupOpen, setIsEditAvatarPopupOpen] = useState(false);
  const [isEditProfilePopupOpen, setIsEditProfilePopupOpen] = useState(false);
  const [isAddPlacePopupOpen, setIsAddPlacePopupOpen] = useState(false);
  const [isDeleteCardPopupOpen, setIsDeleteCardPopupOpen] = useState(false);
  // Ð¡ÑÐµÐ¹Ñ Ð¿Ð¾Ð¿Ð°Ð¿Ð° ÑÐµÐ³Ð¸ÑÑÑÐ°ÑÐ¸Ð¸
  const [isInfoTooltipPopupOpen, setIsInfoTooltipPopupOpen] = useState({ data: {}, isOpen: false });
  // Ð¡ÑÐµÐ¹Ñ Ð¿Ð¾Ð¿Ð°Ð¿Ð° Ñ ÐºÐ°ÑÑÐ¸Ð½ÐºÐ¾Ð¹
  const [selectedCard, setSelectedCard] = useState({ data: {}, isOpen: false })

  function handleEditAvatarClick() {
    setIsEditAvatarPopupOpen(true)
  }
  
  function handleEditProfileClick() {
    setIsEditProfilePopupOpen(true)
  }
  
  function handleAddPlaceClick() {
    setIsAddPlacePopupOpen(true)
  }

  function handleCardClick(dataCard) {
    setSelectedCard({ data: dataCard, isOpen: true})
  }
  
  const [cardId, setCardId] = useState('')
  // ÐÑÐºÑÑÑÑ Ð¿Ð¾Ð¿Ð°Ð¿ ÑÐ´Ð°Ð»ÐµÐ½Ð¸Ñ ÐºÐ°ÑÑÐ¾ÑÐºÐ¸ Ð¸ Ð¾Ð±Ð½Ð¾Ð²Ð¸ÑÑ ÑÑÐµÐ¹Ñ id ÐºÐ°ÑÑÐ¾ÑÐºÐ¸, ÐºÐ¾ÑÐ¾ÑÐ°Ñ Ð¿ÐµÑÐµÐ´Ð°ÐµÑÑÑ Ð¿ÑÐ¸ ÐºÐ»Ð¸ÐºÐµ Ð½Ð° Ð¸ÐºÐ¾Ð½ÐºÑ ÑÐ´Ð°Ð»ÐµÐ½Ð¸Ñ
  function handleDeleteCardClick(cardId) {
    setIsDeleteCardPopupOpen(true)
    setCardId(cardId) 
  }

  function handleCardDelete() {
    api.deleteCard(cardId)
      .then(() => {
        setCards(state => state.filter((card) => card._id !== cardId))
        closeAllPopups()
      })
      .catch(err => console.log(err))
  }

  function closeAllPopups() {
    setIsEditAvatarPopupOpen(false)
    setIsEditProfilePopupOpen(false)
    setIsAddPlacePopupOpen(false)
    setIsDeleteCardPopupOpen(false)

    setIsInfoTooltipPopupOpen({ data: {}, isOpen: false })

    setSelectedCard({ data: {}, isOpen: false })
  }

  useEffect(() => {
    const closeByEscape = (e) => {
      if (e.key === 'Escape') {
        closeAllPopups();
      }
    }

    document.addEventListener('keydown', closeByEscape)
    
    return () => document.removeEventListener('keydown', closeByEscape)
  }, [])

  // Ð¡ÑÐµÐ¹Ñ Ð°Ð²ÑÐ¾ÑÐ¸Ð·Ð°ÑÐ¸Ð¸ Ð¿Ð¾Ð»ÑÐ·Ð¾Ð²Ð°ÑÐµÐ»Ñ
  const [loggedIn, setLoggedIn] = useState(false)
  const [userEmail, setUserEmail] = useState('')

  const history = useHistory();

  const authorize = (jwt) => {
    return auth.getContent(jwt)
      .then((res) => {
        if (res.data) {
          setLoggedIn(true);
          setUserEmail(res.data.email);

          history.push('/')
        }
      })
      .catch(err => console.log(err));
  };

  useEffect(() => {
    const jwt = localStorage.getItem('jwt');
    if (jwt) {
      authorize(jwt);
    }
  }, []);
  
  const onLogin = ({ email, password }) => {
    return auth.authorize(email, password)
      .then((res) => {
        // ÐÑÐ»Ð¸ ÑÐ¾ÐºÐµÐ½ Ð²Ð°Ð»Ð¸Ð´Ð½ÑÐ¹, ÑÐ¾ ÑÐ¾ÑÑÐ°Ð½ÑÐµÐ¼ ÐµÐ³Ð¾ Ð² localStorage, Ð²ÑÐ·ÑÐ²Ð°ÐµÐ¼ Ñ-Ñ authorize Ð´Ð»Ñ Ð¿Ð¾Ð»ÑÑÐµÐ½Ð¸Ñ email Ð¸ Ð²ÑÐ¿Ð¾Ð»Ð½ÑÐµÐ¼ Ð²ÑÐ¾Ð´
        if (res.token) {
          localStorage.setItem('jwt', res.token);
          authorize(res.token)
          setLoggedIn(true);

          history.push('/')

          return res
        } else {
          return res
        }
      })
      .catch(err => console.log(err));
  };

  const onRegister = ({ email, password }) => {
    return auth.register(email, password)
      .then(res => res)
      .catch(err => console.log(err));
  };

  // Ð¿ÑÐ¸ Ð½Ð°Ð¶Ð°ÑÐ¸Ð¸ Ð½Ð° "Ð²ÑÐ¹ÑÐ¸" ÑÐ´Ð°Ð»ÑÐµÐ¼ ÑÐ¾ÐºÐµÐ½ Ð¸Ð· localStorage
  function onSignOut() {
    localStorage.removeItem('jwt')

    setLoggedIn(false)
    setUserEmail('')
    setIsMenuClick(false)

    history.push('/sign-in')
  }

  const [isMenuClick, setIsMenuClick] = useState(false)

  function onMenuClick() {
    !isMenuClick ? setIsMenuClick(true) : setIsMenuClick(false)
  }

  return (
    <CurrentUserContext.Provider value={currentUser}>
      <div className="page">

        <Header 
          userEmail={userEmail} 
          onSignOut={onSignOut} 
          onMenuClick={onMenuClick} 
          isMenuClick={isMenuClick} 
          loggedIn={loggedIn}
        />
        
        <Switch>
          <ProtectedRoute 
            cards={cards}
            exact path="/"
            component={Main}
            loggedIn={loggedIn}
            onCardLike={handleCardLike}
            onCardClick={handleCardClick}
            onAddPlace={handleAddPlaceClick} 
            onEditAvatar={handleEditAvatarClick} 
            onEditProfile={handleEditProfileClick} 
            onCardDeleteClick={handleDeleteCardClick} 
          />

          <Route path="/sign-in">
            <Login onLogin={onLogin} infoTooltip={setIsInfoTooltipPopupOpen} />
          </Route>

          <Route path="/sign-up">
            <Register onRegister={onRegister} infoTooltip={setIsInfoTooltipPopupOpen}/>
          </Route>

          <Route>
            {loggedIn ? (
              <Redirect to="/" />
            ) : (
              <Redirect to="/sign-in" />
            )}
          </Route>
        </Switch>

        <Footer />

         {/* ÐÐ¾Ð¼Ð¿Ð¾Ð½ÐµÐ½ÑÑ Ð¿Ð¾Ð¿Ð°Ð¿Ð¾Ð²  */}
        <EditAvatarPopup isOpen={isEditAvatarPopupOpen} onClose={closeAllPopups} onUpdateAvatar={handleUpdateAvatar} />
        <EditProfilePopup isOpen={isEditProfilePopupOpen} onClose={closeAllPopups} onUpdateUser={handleUpdateUser}/>
        <AddPlacePopup isOpen={isAddPlacePopupOpen} onClose={closeAllPopups} onAddPlace={handleAddPlaceSubmit} />
        <ImagePopup card={selectedCard} onClose={closeAllPopups} />
        <DeleteCardPopup isOpen={isDeleteCardPopupOpen} onClose={closeAllPopups} onCardDelete={handleCardDelete} />
        <InfoTooltip isOpen={isInfoTooltipPopupOpen} onClose={closeAllPopups} />

      </div>
    </CurrentUserContext.Provider>
  );
}



export default App;


