import React from 'react';
import { CurrentUserContext } from '../contexts/CurrentUserContext';
import '../index.css'

function Card(props) {
  const currentUser = React.useContext(CurrentUserContext)

  // ÐÐ¿ÑÐµÐ´ÐµÐ»Ð¸ÑÑ, ÑÐ²Ð»ÑÐµÐ¼ÑÑ Ð»Ð¸ Ð¼Ñ Ð²Ð»Ð°Ð´ÐµÐ»ÑÑÐµÐ¼ ÑÐµÐºÑÑÐµÐ¹ ÐºÐ°ÑÑÐ¾ÑÐºÐ¸
  const isOwn = props.card.owner._id === currentUser._id;
  const cardDeleteButtonClassName = (
    `element__delete ${!isOwn && `element__delete_inactive`}`
  ); 

  // ÐÐ¿ÑÐµÐ´ÐµÐ»Ð¸ÑÑ, ÐµÑÑÑ Ð»Ð¸ Ñ ÐºÐ°ÑÑÐ¾ÑÐºÐ¸ Ð»Ð°Ð¹Ðº, Ð¿Ð¾ÑÑÐ°Ð²Ð»ÐµÐ½Ð½ÑÐ¹ ÑÐµÐºÑÑÐ¸Ð¼ Ð¿Ð¾Ð»ÑÐ·Ð¾Ð²Ð°ÑÐµÐ»ÐµÐ¼
  const isLiked = props.card.likes.some(i => i._id === currentUser._id);
  const cardLikeButtonClassName = (
    `element__like-button ${isLiked && `element__like-button_active`}`
  ); 

  function handleImageClick() {
    props.onCardClick(props.card);
  } 

  function handleLikeClick() {
    props.onCardLike(props.card)
  }

  function handleDeleteClick() {
    props.onCardDeleteClick(props.card._id)
  }

  return (
    <li className="element__item">
      <img className="element__image" onClick={handleImageClick} src={props.card.link} alt={props.card.name} />
      <button className={cardDeleteButtonClassName} type="button" onClick={handleDeleteClick}></button>
      <div className="element__figure">
        <h2 className="element__name">{props.card.name}</h2>
        <div className="element__like-wrapper">
          <button className={cardLikeButtonClassName} type="button" onClick={handleLikeClick}></button>
          <span className="element__like-count">{props.card.likes.length}</span>
        </div>
      </div>
    </li>
  )
}

export default Card;