import React, { useState } from 'react';
import { Link, useHistory } from "react-router-dom";

function Register({ onRegister, infoTooltip }) {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  const history = useHistory();
  
  const resetForm = () => {
    setEmail('');
    setPassword('');
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    onRegister({ email, password })
      .then(res => {
        if (!res.error) {
          infoTooltip({ 
            data: { text: 'ÐÑ ÑÑÐ¿ÐµÑÐ½Ð¾ Ð·Ð°ÑÐµÐ³Ð¸ÑÑÑÐ¸ÑÐ¾Ð²Ð°Ð»Ð¸ÑÑ!', image: 'successfully' }, 
            isOpen: true
          })
          history.push('/sign-in')
        } else {
          infoTooltip({
            data: { text: res.error, image: 'error'}, 
            isOpen: true
          })
        }
      })
      .then(resetForm)
  };

  return (
    <section className="auth">
      <div className="auth__container">
        <h2 className="auth__title">Ð ÐµÐ³Ð¸ÑÑÑÐ°ÑÐ¸Ñ</h2>
        <form className="auth__form" onSubmit={handleSubmit}>
          <input 
            className="input input_type_auth" 
            type="email" 
            placeholder="Email"
            value={email}
            onChange={e => setEmail(e.target.value)}
            />
          <span></span>
          <input 
            className="input input_type_auth" 
            type="password" 
            placeholder="ÐÐ°ÑÐ¾Ð»Ñ"
            value={password}
            onChange={e => setPassword(e.target.value)}
          />
          <span></span>
          <button className="auth__button" type="submit">ÐÐ°ÑÐµÐ³Ð¸ÑÑÑÐ¸ÑÐ¾Ð²Ð°ÑÑÑÑ</button>
        </form>
          <p className="auth__caption">Ð£Ð¶Ðµ Ð·Ð°ÑÐµÐ³Ð¸ÑÑÑÐ¸ÑÐ¾Ð²Ð°Ð½Ñ? <Link className="auth__link" to="/sign-in">ÐÐ¾Ð¹ÑÐ¸</Link></p>
      </div>
    </section>
  );
}

export default Register;