import React, { useState } from 'react';
import { useHistory } from "react-router-dom";

function Login({ onLogin, infoTooltip }) {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  const history = useHistory();

  const resetForm = () => {
    setEmail('');
    setPassword('');
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    onLogin({ email, password })
      .then(res => {
        // ÐÑÐ»Ð¸ ÐµÑÑÑ Ð¾ÑÐ¸Ð±ÐºÐ° ÑÐ¾ Ð¾ÑÐºÑÑÐ²Ð°ÐµÐ¼ Ð¿Ð¾Ð¿Ð°Ð¿ Ñ Ð¾ÑÐ¸Ð±ÐºÐ¾Ð¹, Ð¸Ð½Ð°ÑÐµ Ð°Ð²ÑÐ¾ÑÐ¸Ð·ÑÐµÐ¼ÑÑ
        if (res.message) {
          infoTooltip({
            data: {text: res.message, image: 'error'}, 
            isOpen: true
          })
        } else {
          history.push('/')
        }
      })
      .then(resetForm)
  };

  return (
    <section className="auth">
      <div className="auth__container">
        <h2 className="auth__title">ÐÑÐ¾Ð´</h2>
        <form className="auth__form" onSubmit={handleSubmit}>
          <input 
            className="input input_type_auth" 
            type="email" 
            placeholder="Email"
            value={email}
            onChange={e => setEmail(e.target.value)}
          />
          <span></span>
          <input 
            className="input input_type_auth" 
            type="password" 
            placeholder="ÐÐ°ÑÐ¾Ð»Ñ"
            value={password}
            onChange={e => setPassword(e.target.value)}
          />
          <span></span>
          <button className="auth__button">ÐÐ¾Ð¹ÑÐ¸</button>
        </form>
      </div>
    </section>
  );
}

export default Login;